.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

.footer{
  background-color: #d3cf8c;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}
.jumbotron {
  padding:70px 30px 70px 30px;
  margin:0px auto;
  background: #e6d855 ;
  color:rgb(24, 23, 22);
}
.nav-link{
  font-size: 1.5em;
}
address{
  font-size:80%;
  margin:0px;
  color:#0f0f0f;
}

.navbar-dark {
  background-color: #36d3be;
}
#brand{
  margin: 10px;
  border-radius: 70px;
  width: 80px;
  height: 74px;
 
 background-color: #36d3be;

}

.page-enter{
  opacity: 0.01;
  transform: translateX(-100%);
}

.page-enter-active{
  opacity: 1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}


.page-exit {
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-in-out;
}
html {
  direction: rtl;
  }
  body {
  direction: rtl;
  }
  button,
  input,
  optgroup,
  select,
  textarea {
